import {useMemo} from 'react'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import type {DispositionsPieConfig} from '@bpinc/ad-transport-types'
import {dispositionMetricPrefix} from '@bpinc/ad-transport-types'
import {assertDefined} from '@bpinc/lib-types-helpers'

import type {LegendItem} from './types'


export function useLegendItems(pieConfig: DispositionsPieConfig | undefined) {
    const selectedCampaign = useSelectedCampaign()

    const dispositionsViewItems = useMemo(() => {
        if (
            !selectedCampaign ||
            !pieConfig ||
            pieConfig.dispositions.length === 0
        ) {
            return []
        }

        const shownDispositions = selectedCampaign?.dispositions.reduce<LegendItem[]>((acc, disposition) => {
            const dispositionConfig = pieConfig?.dispositions.find(it => it.dispositionId === disposition.id)

            const isShown = dispositionConfig?.isShownOnLegend ?? true

            if (!dispositionConfig || !isShown) {
                return acc
            }

            const dispositionMetricId = `${dispositionMetricPrefix}${disposition.id}` as const
            const metricStringValue = selectedCampaign.dispositionsMetrics[dispositionMetricId]
            const metricValue = Number(metricStringValue)
            const count = Number.isNaN(metricValue) ? 0 : metricValue

            return [
                ...acc,
                {
                    id: disposition.id,
                    color: dispositionConfig?.color,
                    name: disposition.name,
                    count,
                },
            ]
        }, [])

        return shownDispositions || []
    }, [selectedCampaign, pieConfig])

    const groupsViewItems = useMemo(() => {
        if (!selectedCampaign) {
            return []
        }

        const groupNames = Array.from(new Set(
            selectedCampaign.dispositions.map(it => it.group),
        ))
        return groupNames.map<LegendItem>(groupName => {
            const groupConfig = pieConfig?.groups.find(it => it.name === groupName)
            assertDefined(groupConfig, `Group config for ${groupName} not found`)

            const dispositionsOfTheGroup = selectedCampaign.dispositions.filter(it => it.group === groupName)

            const count = dispositionsOfTheGroup.reduce((countAcc, disposition) => {
                const dispositionMetricId = `${dispositionMetricPrefix}${disposition.id}` as const
                const metricStringValue = selectedCampaign.dispositionsMetrics[dispositionMetricId]
                const metricValue = Number(metricStringValue)
                const dispositionCount = Number.isNaN(metricValue) ? 0 : metricValue

                return countAcc + dispositionCount
            }, 0)

            return {
                id: groupName,
                color: groupConfig?.color,
                name: groupName,
                count,
            }
        }, [])
    }, [selectedCampaign, pieConfig?.groups])

    const legendItems = useMemo(() => {
        return pieConfig?.displayMode === 'dispositions'
            ? dispositionsViewItems
            : groupsViewItems
    }, [pieConfig?.displayMode, dispositionsViewItems, groupsViewItems])

    return legendItems
}
