import {useCallback, useMemo} from 'react'

import {campaignsListsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import {
    type CampaignListsColumnGroup,
    type CampaignListsColumnItem,
    type CampaignListsTableColumn,
    type CampaignListsTableColumnsConfig,
    checkIsCampaignListsColumnGroup,
} from '@bpinc/ad-transport-types'
import type {ColumnsEditorColumn, ColumnsEditorConfig, ColumnsEditorGroup} from '@bpinc/lib-ui-elements'
import {useLocalizeString} from '@bpinc/state-localization-context'


type CampaignsTableMeta = {
    beforeEditing: CampaignListsColumnGroup | CampaignListsColumnItem
}

export function useCampaignListsTableConfigAdapter() {
    const toEditing = useConvertConfigToEditing()

    return useMemo(() => ({
        toEditing,
        fromEditing,
    }), [toEditing])
}

function useConvertConfigToEditing() {
    const localize = useLocalizeString()

    return useCallback((config: CampaignListsTableColumnsConfig) => {
        return config.map(it => {
            if (checkIsCampaignListsColumnGroup(it)) {
                const group: ColumnsEditorGroup<CampaignsTableMeta> = {
                    id: it.id,
                    defaultName: localize(campaignsListsColumnsLocalizationMap[it.id]),
                    customName: it.customName,
                    isGroup: true,
                    columns: it.columns.map(columnToTreeItem),
                    meta: {
                        beforeEditing: it,
                    },
                }

                return group
            }

            const item = columnToTreeItem(it)

            return item


            function columnToTreeItem(configItem: CampaignListsColumnItem): ColumnsEditorColumn<CampaignsTableMeta> {
                return {
                    id: configItem.id,
                    defaultName: localize(campaignsListsColumnsLocalizationMap[configItem.id]),
                    customName: configItem.customName,
                    isActive: configItem.isShown,
                    meta: {
                        beforeEditing: configItem,
                    },
                }
            }
        })
    }, [localize])
}

function fromEditing(configItem: ColumnsEditorConfig<CampaignsTableMeta> | undefined): CampaignListsTableColumnsConfig {
    const newConfig = configItem?.map<CampaignListsColumnGroup | CampaignListsColumnItem>(it => {
        if (it.isGroup) {
            const group: CampaignListsColumnGroup = {
                ...it.meta?.beforeEditing,
                id: it.id as CampaignListsTableColumn,
                customName: it.customName,
                columns: it.columns.map(treeItemToColumn),
            }

            return group
        }

        return treeItemToColumn(it)

        function treeItemToColumn(item: ColumnsEditorColumn<CampaignsTableMeta>): CampaignListsColumnItem {
            const itemMetaBeforeEditing = item.meta?.beforeEditing
            if (!itemMetaBeforeEditing || checkIsCampaignListsColumnGroup(itemMetaBeforeEditing)) {
                throw new Error('Invalid column meta data')
            }
            return {
                ...itemMetaBeforeEditing,
                customName: item.customName,
                isShown: item.isActive,
            }
        }
    })

    return newConfig || []
}
