import type {ReactNode} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


type SectionHeaderProps = {
    className?: string
    children: ReactNode
}

export const SectionHeader: React.FC<SectionHeaderProps> = ({className, children}) => {
    return (
        <div className={tcn('flex flex-wrap justify-between', className)}>
            {children}
        </div>
    )
}
