import React from 'react'

import type {DispositionsPieConfig} from '@bpinc/ad-transport-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {Chart} from './Chart'
import {Legend} from './Legend'
import {Title} from './Title'
import type {OnColorChange, OnColorTitle} from './types'


export function ControlledDispositionsPie({
    pieConfig,
    onChangeColor,
    onChangeTitle,
    className,
}: {
    pieConfig: DispositionsPieConfig | undefined
    onChangeColor?: OnColorChange
    onChangeTitle?: OnColorTitle
    className?: string
}) {
    return (
        <div className={tcn('flex h-full flex-col items-stretch overflow-hidden', className)}>
            <Title pieConfig={pieConfig} onChangeTitle={onChangeTitle} />

            <div className="mt-2 flex grow overflow-hidden pr-6">
                <div className="w-52 shrink-0">
                    <Chart pieConfig={pieConfig} />
                </div>

                <div className="flex min-w-44 grow flex-col items-stretch">
                    <Legend
                        pieConfig={pieConfig}
                        onChangeColor={onChangeColor}
                    />
                </div>
            </div>
        </div>
    )
}
