import React, {useCallback, useEffect, useState} from 'react'

import {useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import type {CampaignListId} from '@bpinc/ad-transport-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Checkbox} from '@bpinc/lib-ui-elements'


type StartStopTogglerProps = {
    listId: CampaignListId
}

export const StartStopToggle: React.FC<StartStopTogglerProps> = ({
    listId,
}) => {
    const {enableDisableList} = useCampaignsStateMethods()
    const selectedCampaign = useSelectedCampaign()
    const list = selectedCampaign?.lists.find(it => it.id === listId)

    const isListEnabled = list?.metrics.is_enabled === '1'

    const [isDisabled, setIsDisabled] = useState(false)

    const handleToggle = useCallback(async (newIsEnabled: boolean) => {
        if (isDisabled || !selectedCampaign?.id) {
            return
        }
        setIsDisabled(true)
        await enableDisableList(selectedCampaign.id, listId, newIsEnabled)
    }, [enableDisableList, isDisabled, listId, selectedCampaign?.id])

    useEffect(() => { // when the request is done, we can allow the user to interact with the toggle again
        setIsDisabled(false)
    }, [isListEnabled])

    return (
        <Checkbox
            toggle
            checked={isListEnabled}
            disabled={isDisabled}
            labelClassName={tcn('min-w-[50px]')}
            onChange={handleToggle}
        />
    )
}
