import React, {useCallback} from 'react'

import type {CampaignListId} from '@bpinc/ad-transport-types'
import {SmProgress} from '@bpinc/lib-external-ui-elements'
import {formatDurationShort} from '@bpinc/lib-helpers'
import {type ItemRendererType, type ResizeTableColumn, type TableItem} from '@bpinc/lib-ui-elements'

import {Remaining} from './Remaining'
import {StartStopToggle} from './StartStopToggle'


export const useItemRenderer = () => {
    const renderTime = useCallback((value: unknown): React.ReactNode => {
        if (typeof value !== 'string' && typeof value !== 'number') {
            return null
        }

        const time = formatDurationShort(value)
        return time
    }, [])

    const renderAnything = useCallback((value: unknown): React.ReactNode => {
        if (typeof value !== 'string' && typeof value !== 'number') {
            return null
        }
        return `${value}`
    }, [])

    return useCallback((item: TableItem, column: ResizeTableColumn): ItemRendererType => {
        const itemValue = item[column.name]

        switch (column.name) {
            case 'records_completed_ratio': {
                return {
                    value: <SmProgress percent={item.records_completed_ratio as string} className="mb-0" progress="percent" success />,
                    meta: undefined,
                }
            }

            case 'list_last_updated': {
                return {
                    value: renderTime(itemValue),
                    meta: undefined,
                }
            }

            case 'is_enabled': {
                return {
                    value: <StartStopToggle listId={item.list_id as CampaignListId} />,
                    meta: undefined,
                }
            }

            case 'records_remaining': {
                return {
                    value: (
                        <Remaining listId={item.list_id as CampaignListId}>
                            {renderAnything(itemValue)}
                        </Remaining>
                    ),
                    meta: undefined,
                }
            }

            case 'name':
            case 'priority':
            case 'ratio':
            case 'records_total':
            case 'records_dialable':
            case 'records_completed':
            case 'records_completed_per_day':
            case 'records_in_dnc':
            case 'records_expired':
            case 'records_with_assigned_agents_completed':
            case 'records_with_assigned_agents_remaining':
            default: {
                return {
                    value: renderAnything(itemValue),
                    meta: undefined,
                }
            }
        }
    }, [renderAnything, renderTime])
}
