import React, {useMemo} from 'react'
import {throttle} from 'lodash-es'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import type {DispositionsPieConfig} from '@bpinc/ad-transport-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {OnColorChange} from './types'
import {useLegendItems} from './useLegendItems'


export function Legend({
    pieConfig,
    onChangeColor,
}: {
    pieConfig: DispositionsPieConfig | undefined
    onChangeColor?: OnColorChange
}) {
    const {t} = useLocalizationState()
    const selectedCampaign = useSelectedCampaign()
    const legendItems = useLegendItems(pieConfig)

    const throttledChangeColor = useMemo(() => throttle((e: React.ChangeEvent<HTMLInputElement>) => {
        onChangeColor?.(e.target.dataset.dispositionId!, e.target.value)
    }, 200), [onChangeColor])

    if (!selectedCampaign) {
        return (
            <div className="flex justify-center py-2">
                {t.CAMPAIGN_MONITOR.no_campaign_selected()}
            </div>
        )
    }

    if (!legendItems.length) {
        return (
            <div className="flex justify-center py-2">
                {t.CAMPAIGN_MONITOR.no_dispositions()}
            </div>
        )
    }

    return (
        <>
            <p className="shrink-0 text-right font-bold">
                {t.CAMPAIGN_MONITOR.count()}
            </p>

            <div className={tcn('grow overflow-auto pb-2 pr-1')}>
                {legendItems.map(legendItem => (
                    <div key={legendItem.id} className="mb-1 flex w-full justify-between gap-2">
                        <div className="relative flex gap-2">
                            <label>
                                {onChangeColor && (
                                    <input
                                        type="color"
                                        data-disposition-id={legendItem.id}
                                        className="absolute z-[-1] opacity-0"
                                        onChange={throttledChangeColor}
                                        value={legendItem.color}
                                    />
                                )}
                                <div
                                    className="pointer relative top-1 size-3"
                                    style={{backgroundColor: legendItem.color}}
                                />
                            </label>
                            <p className="min-w-1/2 max-w-40 shrink-0">{legendItem.name}</p>
                        </div>

                        <p className="text-wrap">{legendItem.count}</p>
                    </div>
                ))}
            </div>
        </>
    )
}
