import {useCallback, useMemo} from 'react'

import {campaignsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import type {CampaignDetailsTableColumnItem, CampaignDetailsTableColumnsConfig} from '@bpinc/ad-transport-types'
import type {ColumnsEditorColumn, ColumnsEditorConfig} from '@bpinc/lib-ui-elements'
import {useLocalizeString} from '@bpinc/state-localization-context'


export type CampaignDetailsTableMeta = {
    beforeEditing: CampaignDetailsTableColumnItem
}

export function useCampaignDetailsTableConfigAdapter() {
    const toEditing = useConvertConfigToEditing()

    return useMemo(() => ({
        toEditing,
        fromEditing,
    }), [toEditing])
}

function useConvertConfigToEditing() {
    const localize = useLocalizeString()

    return useCallback((config: CampaignDetailsTableColumnsConfig) => {
        return config.map(columnToTreeItem)

        function columnToTreeItem(configItem: CampaignDetailsTableColumnItem): ColumnsEditorColumn<CampaignDetailsTableMeta> {
            return {
                id: configItem.id,
                defaultName: localize(campaignsColumnsLocalizationMap[configItem.id]),
                customName: configItem.customName,
                isActive: configItem.isShown,
                meta: {
                    beforeEditing: configItem,
                },
            }
        }
    }, [localize])
}

function fromEditing(configItem: ColumnsEditorConfig<CampaignDetailsTableMeta> | undefined): CampaignDetailsTableColumnsConfig | undefined {
    const newConfig = configItem?.reduce<CampaignDetailsTableColumnsConfig>((acc, it) => {
        if (!it.isGroup) {
            acc.push(treeItemToColumn(it))
        }

        return acc

        function treeItemToColumn(item: ColumnsEditorColumn<CampaignDetailsTableMeta>): CampaignDetailsTableColumnItem {
            const itemMetaBeforeEditing = item.meta?.beforeEditing
            if (!itemMetaBeforeEditing) {
                throw new Error('Invalid column meta data')
            }
            return {
                ...itemMetaBeforeEditing,
                customName: item.customName,
                isShown: item.isActive,
            }
        }
    }, [])

    return newConfig
}
