import React, {useCallback, useMemo, useState} from 'react'

import {useCampaignsState, useCampaignsStateMethods} from '@bpinc/ad-campaigns-state-context'
import type {CampaignTableType} from '@bpinc/ad-campaigns-state-types'
import {defaultCampaignDetailsTableColumnsConfig} from '@bpinc/ad-campaigns-state-types'
import type {CampaignId} from '@bpinc/ad-transport-types'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {ColumnsEditorColumn, ColumnsEditorConfig} from '@bpinc/lib-ui-elements'
import {ColumnsEditor, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import type {CampaignDetailsTableMeta} from './hooks/useCampaignDetailsTableConfigAdapter'
import {useCampaignDetailsTableConfigAdapter} from './hooks/useCampaignDetailsTableConfigAdapter'
import {useCampaignListsTableConfigAdapter} from './hooks/useCampaignListsTableConfigAdapter'
import {useCampaignsTableConfigAdapter} from './hooks/useCampaignsTableConfigAdapter'


type ManageMetricsModalProps = CommonModalProps & {
    table: CampaignTableType
    campaignId?: CampaignId
}

export const ManageMetricsModal: React.FC<ManageMetricsModalProps> = props => {
    const {
        onClose,
        table,
        campaignId,
        ...restProps
    } = props

    const {t} = useLocalizationState()
    const {toEditing: convertCampaignsConfigToEditing, fromEditing: convertCampaignsConfigFromEditing} = useCampaignsTableConfigAdapter()
    const {toEditing: convertCampaignDetailsConfigToEditing, fromEditing: convertCampaignDetailsConfigFromEditing} = useCampaignDetailsTableConfigAdapter()
    const {toEditing: convertCampaignListsConfigToEditing, fromEditing: convertCampaignListsConfigFromEditing} = useCampaignListsTableConfigAdapter()

    const {pageConfig} = useCampaignsState()
    const {updatePageConfig} = useCampaignsStateMethods()

    const campaignDetailsTableColumnsConfig = useMemo(() => {
        if (table !== 'campaignDetails' || !campaignId) {
            return []
        }

        return pageConfig.CAMPAIGN_DETAILS_TABLE_COLUMNS_CONFIG[campaignId]
            || defaultCampaignDetailsTableColumnsConfig
    }, [pageConfig, campaignId, table])

    const defaultCampaignsTableEditingConfig = useMemo(
        () => convertCampaignsConfigToEditing(pageConfig.CAMPAIGNS_TABLE_COLUMNS_CONFIG),
        [pageConfig.CAMPAIGNS_TABLE_COLUMNS_CONFIG, convertCampaignsConfigToEditing],
    )

    const defaultCampaignDetailsTableEditingConfig = useMemo(
        () => convertCampaignDetailsConfigToEditing(campaignDetailsTableColumnsConfig),
        [campaignDetailsTableColumnsConfig, convertCampaignDetailsConfigToEditing],
    )

    const defaultCampaignListsTableEditingConfig = useMemo(
        () => convertCampaignListsConfigToEditing(pageConfig.CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG),
        [pageConfig.CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG, convertCampaignListsConfigToEditing],
    )

    const [editingCampaignsTableConfig, setEditingCampaignsTableConfig] = useState(defaultCampaignsTableEditingConfig)
    const [editingCampaignDetailsTableConfig, setEditingCampaignDetailsTableConfig] = useState(defaultCampaignDetailsTableEditingConfig)
    const [editingCampaignListsTableConfig, setEditingCampaignListsTableConfig] = useState(defaultCampaignListsTableEditingConfig)

    const editingConfig = useMemo(() => {
        if (table === 'campaignDetails') {
            return editingCampaignDetailsTableConfig
        } else if (table === 'campaigns') {
            return editingCampaignsTableConfig
        } else if (table === 'lists') {
            return editingCampaignListsTableConfig
        }
        return []
    }, [table, editingCampaignDetailsTableConfig, editingCampaignsTableConfig, editingCampaignListsTableConfig])

    const onChange = useCallback((newConfig: ColumnsEditorConfig<any>) => {
        if (table === 'campaignDetails') {
            setEditingCampaignDetailsTableConfig(newConfig as ColumnsEditorColumn<CampaignDetailsTableMeta>[])
        } else if (table === 'campaigns') {
            setEditingCampaignsTableConfig(newConfig)
        } else if (table === 'lists') {
            setEditingCampaignListsTableConfig(newConfig)
        }
    }, [table, setEditingCampaignDetailsTableConfig, setEditingCampaignsTableConfig, setEditingCampaignListsTableConfig])


    const onSave = useCallback(() => {
        if (table === 'campaignDetails') {
            assertDefined(campaignId)
            const newConfig = convertCampaignDetailsConfigFromEditing(editingCampaignDetailsTableConfig)
            updatePageConfig({
                ...pageConfig,
                CAMPAIGN_DETAILS_TABLE_COLUMNS_CONFIG: {
                    ...pageConfig.CAMPAIGN_DETAILS_TABLE_COLUMNS_CONFIG,
                    [campaignId]: newConfig,
                },
            })
        } else if (table === 'campaigns') {
            const newConfig = convertCampaignsConfigFromEditing(editingCampaignsTableConfig)
            updatePageConfig({
                ...pageConfig,
                CAMPAIGNS_TABLE_COLUMNS_CONFIG: newConfig,
            })
        } else if (table === 'lists') {
            const newConfig = convertCampaignListsConfigFromEditing(editingCampaignListsTableConfig)
            updatePageConfig({
                ...pageConfig,
                CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG: newConfig,
            })
        }

        onClose()
    }, [
        table,
        campaignId,
        editingCampaignDetailsTableConfig,
        editingCampaignsTableConfig,
        convertCampaignDetailsConfigFromEditing,
        convertCampaignsConfigFromEditing,
        updatePageConfig,
        onClose,
        pageConfig,
        editingCampaignListsTableConfig,
        convertCampaignListsConfigFromEditing,
    ])

    return (
        <Modal
            flex
            onClose={onClose}
            className="relative max-h-[calc(100vh-4rem)] !w-auto max-w-[calc(100vw-2em)] flex-col overflow-hidden"
            aria-labelledby="manage_metrics"
            data-testid="manageMetricsModal"
            {...restProps}
        >
            <Modal.Header id="manage_metrics" role="heading">
                {t.SUPERVISION_PAGE.manage_metrics_modal_title()}
            </Modal.Header>

            <Modal.Content className="flex flex-auto flex-col overflow-hidden">
                <ColumnsEditor
                    value={editingConfig}
                    onChange={onChange}
                />
            </Modal.Content>

            <Modal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                    data-testid="cancel-button"
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton onClick={onSave} primary data-testid="ok-button">
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
