import {useCallback, useState} from 'react'
import produce from 'immer'

import {useLocalStorageForState} from '@bpinc/ad-local-settings-context'


type CollapsedGroupsIds = {
    [groupId: string]: boolean
}

const initialState: CollapsedGroupsIds = {}

export function useCollapsedTableGroupsIds(tableName: TableName) {
    const [collapsedGroupIds, setCollapsedGroupIds] = useState<CollapsedGroupsIds>(initialState)

    const storageKey = getStorageKey(tableName)
    useLocalStorageForState(collapsedGroupIds, storageKey, setCollapsedGroupIds, initialState)

    const toggleCollapsedGroup = useCallback((groupId: string) => {
        setCollapsedGroupIds(
            produce(draft => {
                draft[groupId] = !draft[groupId]
            }),
        )
    }, [])

    return {
        collapsedGroupIds,
        toggleCollapsedGroup,
    }
}


const storageKeyMap = {
    campaigns: 'adCampaignsListTableCollapsedGroupIds',
    details: 'adCampaignDetailsTableCollapsedGroupIds',
    lists: 'adListsTableCollapsedGroupIds',
} as const

type TableName = keyof typeof storageKeyMap
type StorageKey = typeof storageKeyMap[keyof typeof storageKeyMap]
function getStorageKey<T extends TableName>(tableName: T): StorageKey {
    return storageKeyMap[tableName] as StorageKey
}
