import {useMemo} from 'react'

import type {SortingSettings, TableItem} from '@bpinc/lib-ui-elements'


type TableDataParams = {
    data: TableItem[]
    sortingSettings?: SortingSettings
}

export const useSortedTableData = ({
    data,
    sortingSettings,
}: TableDataParams) => {
    return useMemo(() => {
        if (!sortingSettings) {
            return data
        }

        const {column, sorting} = sortingSettings

        return data.toSorted((a, b) => {
            const [v1, v2] = [a[column], b[column]]

            if (typeof v1 === 'number' && typeof v2 === 'number') {
                return sorting === 'ASC' ? v1 - v2 : v2 - v1
            }

            if (typeof v1 === 'string' && typeof v2 === 'string') {
                return sorting === 'ASC'
                    ? v1.localeCompare(v2)
                    : v2.localeCompare(v1)
            }

            return 0
        })
    }, [data, sortingSettings])
}
