import React, {useMemo} from 'react'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {type DispositionsPieConfig, dispositionMetricPrefix} from '@bpinc/ad-transport-types'
import {Chart as Highchart} from '@bpinc/lib-highcharts'
import {assertDefined, typedEntries} from '@bpinc/lib-types-helpers'


export function Chart({
    pieConfig,
}: {
    pieConfig: DispositionsPieConfig | undefined
}) {
    const selectedCampaign = useSelectedCampaign()

    const allMetricsSumm = useMemo(() => {
        if (!selectedCampaign) {
            return 0
        }

        return typedEntries(selectedCampaign.dispositionsMetrics).reduce((acc, [key, value]) => {
            return acc + Number(value)
        }, 0)
    }, [selectedCampaign])

    const dispositionsViewItems = useMemo(() => {
        if (!pieConfig || !selectedCampaign) {
            return []
        }

        return selectedCampaign.dispositions
            .filter(disposition => {
                const dispositionConfig = pieConfig?.dispositions.find(it => it.dispositionId === disposition.id)
                return dispositionConfig?.isShownOnPie
            })
            .map(disposition => {
                const dispositionConfig = pieConfig?.dispositions.find(it => it.dispositionId === disposition.id)
                assertDefined(dispositionConfig, 'Desposition config not found')

                const metricId = `${dispositionMetricPrefix}${disposition.id}` as const
                const metricCount = Number(selectedCampaign.dispositionsMetrics[metricId] || 0)
                const dispositionPercent = (metricCount / allMetricsSumm) * 100

                return {
                    name: disposition.name,
                    y: dispositionPercent,
                    color: dispositionConfig.color,
                }
            })
    }, [pieConfig, selectedCampaign, allMetricsSumm])

    const groupsViewItems = useMemo(() => {
        if (!pieConfig || !selectedCampaign) {
            return []
        }

        const groupNames = Array.from(new Set(selectedCampaign.dispositions.map(it => it.group)))
        return groupNames.map(groupName => {
            const groupConfig = pieConfig.groups.find(it => it.name === groupName)
            assertDefined(groupConfig, 'Group config not found')

            const count = selectedCampaign.dispositions
                .filter(it => it.group === groupName)
                .reduce((acc, disposition) => {
                    const metricId = `${dispositionMetricPrefix}${disposition.id}` as const
                    const metricCount = Number(selectedCampaign.dispositionsMetrics[metricId] || 0)
                    return acc + metricCount
                }, 0)

            const percent = (count / allMetricsSumm) * 100

            return {
                name: groupName,
                y: percent,
                color: groupConfig.color,
            }
        })
    }, [pieConfig, selectedCampaign, allMetricsSumm])

    const seriesItems = useMemo(() => {
        if (allMetricsSumm === 0) {
            return [{
                name: 'No data',
                y: 100,
                color: '#cccccc',
            }]
        }

        return pieConfig?.displayMode === 'dispositions'
            ? dispositionsViewItems
            : groupsViewItems
    }, [allMetricsSumm, pieConfig, dispositionsViewItems, groupsViewItems])

    const chartOptions = useMemo<Highcharts.Options>(() => {
        return {
            chart: {
                plotBorderWidth: 0,
                plotShadow: false,
                type: 'pie',
                height: 200,
            },
            title: {
                text: '',
            },
            tooltip: {
                pointFormat: '<b>{point.percentage:.1f}%</b>',
            },
            plotOptions: {
                pie: {
                    cursor: 'pointer',
                    dataLabels: {
                        enabled: true,
                        format: '{point.percentage:.1f}%',
                        distance: -30,
                        filter: {
                            property: 'percentage',
                            operator: '>',
                            value: 4,
                        },
                    },
                },
                series: {
                    animation: {
                        duration: 0,
                    },
                },
            },
            series: [{
                type: 'pie',
                data: seriesItems,
            }],
        } satisfies Highcharts.Options
    }, [seriesItems])

    return (
        <div>
            <Highchart options={chartOptions} />
        </div>
    )
}

