import React, {useCallback, useDeferredValue, useMemo, useState} from 'react'

import {useCampaignsState} from '@bpinc/ad-campaigns-state-context'
import type {CampaignId} from '@bpinc/ad-transport-types'
import {getElementFontString, useResizeObserver} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getFitStrings} from '../../utils/getFitStrings'


export const CampaignsTableTeams: React.FC<{
    campaignId: CampaignId
}> = ({
    campaignId,
}) => {
    const t = useLocalizationState('t')
    const {campaignItems} = useCampaignsState()

    const allCampaignTeamsNames = useMemo(() => {
        const campaign = campaignItems.find(item => item.id === campaignId)
        if (!campaign) {
            return []
        }
        const teams = campaign?.teams || []
        const teamNames = teams.map(team => team.name || '')
        return teamNames
    }, [campaignItems, campaignId])

    const [containerEl, setContainerEl] = useState<HTMLElement | null>(null)
    const wrapperResizeObserver = useResizeObserver(containerEl)

    const containerWidth = wrapperResizeObserver?.contentRect.width || 0
    const defferedContainerWidth = useDeferredValue(containerWidth)

    const [textEl, setTextEl] = useState<HTMLElement | null>(null)

    const font = useMemo(() => {
        if (!textEl) {
            return undefined
        }

        return getElementFontString(textEl)
    }, [textEl])

    const after = useCallback(({restCount}: {restCount: number}) => {
        if (restCount === 0) {
            return ''
        }

        return t.KNOWLEDGE_BASE.plus_more(restCount)
    }, [t])

    const {
        fittedList,
        restCount,
    } = useMemo(() => {
        if (!font) {
            return {
                fittedList: [],
                restCount: 0,
            }
        }

        const fittedListInner = getFitStrings({
            strings: allCampaignTeamsNames,
            containerWidth: defferedContainerWidth,
            font,
            after,
        })

        const restCountInner = allCampaignTeamsNames.length - fittedListInner.length

        return {
            fittedList: fittedListInner,
            restCount: restCountInner,
        }
    }, [defferedContainerWidth, allCampaignTeamsNames, font, after])

    const text = fittedList.join(', ') + (after?.({restCount}) || '')

    const onClick = useCallback((e: React.MouseEvent) => {
        e.stopPropagation()
        console.log('TODO: show teams modal')
    }, [])

    return (
        <button
            ref={setContainerEl}
            className="w-full overflow-hidden text-link hover:underline"
            onClick={onClick}
        >
            <span ref={setTextEl} className="whitespace-nowrap">
                {text}
            </span>
        </button>
    )
}
