import {useCallback} from 'react'

import {useCampaignsState, useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {ManageMetricsModal} from '../../modals/ManageMetricsModal'
import type {GetOptions} from '../TableActionsButton'
import {useGoRemainingRecords} from './useGoRemainingRecords'


export function useTableMenuOptions(): GetOptions {
    const {t} = useLocalizationState()
    const {enableDisableList} = useCampaignsStateMethods()
    const openManageMetricsModal = useModalComponentOpener(ManageMetricsModal)
    const {selectedListId} = useCampaignsState()
    const selectedCampaign = useSelectedCampaign()
    const goRemainingRecords = useGoRemainingRecords()
    const selectedList = selectedCampaign?.lists.find(it => it.id === selectedListId)

    const isListEnabled = selectedList?.metrics.is_enabled

    return useCallback(({toggleShow}) => [
        {
            children: t.SUPERVISION_PAGE.actions_menu_option_manage_metrics(),
            onClick: () => {
                openManageMetricsModal({table: 'lists'})
                toggleShow()
            },
        },
        {
            children: isListEnabled
                ? t.CAMPAIGN_MONITOR.campaignTeam_button_disable()
                : t.CAMPAIGN_MONITOR.campaignTeam_button_enable(),
            disabled: !selectedList,
            onClick: () => {
                if (!selectedCampaign || !selectedList) {
                    return
                }
                enableDisableList(selectedCampaign.id, selectedList.id, !isListEnabled)
                toggleShow()
            },
        },
        {
            children: t.CAMPAIGN_MONITOR.get_remaining_records(),
            disabled: !selectedCampaign || !selectedList,
            onClick: () => {
                assertDefined(selectedCampaign)
                assertDefined(selectedList)

                goRemainingRecords({
                    campaignId: selectedCampaign.id,
                    listId: selectedList.id,
                })
                toggleShow()
            },
        },
    ], [openManageMetricsModal, t, selectedCampaign, selectedList, enableDisableList, isListEnabled, goRemainingRecords])
}
