import React, {useCallback, useState} from 'react'
import produce from 'immer'
import {createStore, Provider, useAtom} from 'jotai'

import {useCampaignsState, useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {ControlledDispositionsPie} from '../../components/ControlledDispositionsPie'
import {editingConfigAtom} from './atoms'
import {ShownDispositionsEditor} from './ShownDispositionsEditor'


export const DispositionsPieEditorModal: React.FC<CommonModalProps> = props => {
    const {pageConfig} = useCampaignsState()
    const selectedCampaign = useSelectedCampaign()

    const selectedCampaignPieConfig = selectedCampaign?.id
        ? pageConfig.DISPOSITIONS_PIE_CONFIG[selectedCampaign.id]
        : undefined

    const [store] = useState(() => {
        const innerStore = createStore()
        innerStore.set(editingConfigAtom, selectedCampaignPieConfig)
        return innerStore
    })

    return (
        <Provider store={store}>
            <DispositionsPieEditorModalContent {...props} />
        </Provider>
    )
}

function DispositionsPieEditorModalContent({
    onClose,
    mountNode,
    ...restProps
}: CommonModalProps) {
    const {t} = useLocalizationState()
    const {pageConfig} = useCampaignsState()
    const selectedCampaign = useSelectedCampaign()
    const {updatePageConfig} = useCampaignsStateMethods()

    const [editingConfig, setEditingConfig] = useAtom(editingConfigAtom)

    const onSave = () => {
        if (!selectedCampaign?.id) {
            return
        }

        updatePageConfig({
            ...pageConfig,
            DISPOSITIONS_PIE_CONFIG: {
                ...pageConfig.DISPOSITIONS_PIE_CONFIG,
                [selectedCampaign.id]: editingConfig,
            },
        })
        onClose()
    }

    const changeDispositionColor = useCallback((dispositionId: string, color: string) => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }

            const dispositionConfig = prev.dispositions.find(it => it.dispositionId === dispositionId)
            if (!dispositionConfig) {
                return
            }

            dispositionConfig.color = color
        }))
    }, [setEditingConfig])

    const changeDispositionTitle = useCallback((title: string) => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            prev.title = title
        }))
    }, [setEditingConfig])

    if (!editingConfig) {
        return null
    }

    return (
        <Modal
            flex
            onClose={onClose}
            mountNode={mountNode}
            className="relative max-h-[calc(100vh-4rem)] !w-auto max-w-[calc(100vw-2em)] flex-col overflow-hidden"
            {...restProps}
        >
            <Modal.Header id="manage_metrics" role="heading">
                {t.CAMPAIGN_MONITOR.chart_settings()}
            </Modal.Header>

            <Modal.Content className="flex w-full flex-auto flex-col overflow-hidden p-0">
                <div className="flex min-h-[387px] items-stretch overflow-x-auto overflow-y-hidden">
                    <div className="shrink-0 border-r pt-2">
                        <ControlledDispositionsPie
                            pieConfig={editingConfig}
                            onChangeColor={changeDispositionColor}
                            onChangeTitle={changeDispositionTitle}
                        />
                    </div>

                    <div className="overflow-y-auto pl-6 pr-2 pt-2">
                        <ShownDispositionsEditor />
                    </div>
                </div>
            </Modal.Content>

            <Modal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                    data-testid="cancel-button"
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>

                <SemanticButton onClick={onSave} primary data-testid="ok-button">
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
