import type {FC} from 'react'
import React, {useMemo} from 'react'

import {useCampaignsState, useCampaignsStateMethods} from '@bpinc/ad-campaigns-state-context'
import {CLOSEABLE_TABS_TYPES} from '@bpinc/ad-campaigns-state-types'
import type {TabI} from '@bpinc/lib-ui-elements'
import {Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const CampaignsPageTabs: FC = () => {
    const {t} = useLocalizationState()
    const {tabs, selectedCampaignTabId, campaignItems} = useCampaignsState()
    const {selectTab, closeTab} = useCampaignsStateMethods()

    const computedTabs = useMemo(() => {
        return tabs.map(tab => {
            const title = (() => {
                switch (tab.type) {
                    case 'voiceCampaigns':
                        return t.CAMPAIGN_MONITOR.voice_campaigns()
                    case 'messagingCampaigns':
                        return t.CAMPAIGN_MONITOR.messaging_campaigns()
                    case 'campaignResults': {
                        const campaign = campaignItems.find(it => it.id === tab.campaignId)
                        const campaignName = campaign?.fields.name ?? ''
                        return campaignName
                            ? `${campaignName}.${t.CAMPAIGN_MONITOR.preview_detailed_results()}`
                            : t.CAMPAIGN_MONITOR.preview_detailed_results()
                    }
                    case 'listRemainingRecords': {
                        const campaign = campaignItems.find(it => it.id === tab.campaignId)
                        const list = campaign?.lists.find(it => it.id === tab.listId)

                        return list
                            ? `${list.metrics.name}. ${t.CAMPAIGN_MONITOR.remaining_records()}`
                            : t.CAMPAIGN_MONITOR.remaining_records()
                    }
                    default:
                        return 'No title'
                }
            })()

            const closeable = CLOSEABLE_TABS_TYPES.includes(tab.type)

            return {
                id: tab.id,
                title,
                closeable,
            } satisfies TabI<string>
        })
    }, [tabs, t, campaignItems])

    return (
        <header className="flex h-[50px] items-center justify-between bg-[--campaigns-header-bg] py-1.5 pr-6">
            <Tabs
                tabs={computedTabs}
                activeTabId={selectedCampaignTabId}
                headerClassName="bg-transparent border-b-transparent"
                tabsContentClassName="hidden"
                onChange={selectTab}
                onTabClose={closeTab}
            />
        </header>
    )
}
