import React from 'react'

import {useSelectedTab} from '@bpinc/ad-campaigns-state-context'

import {ListRemainingRecordsTab} from './ListRemainingRecordsTab'
import {VoiceCampaignsTab} from './VoiceCampaignsTab'


export const TabContent: React.FC = () => {
    const selectedTab = useSelectedTab()

    switch (selectedTab?.type) {
        case 'voiceCampaigns': {
            return <VoiceCampaignsTab />
        }
        case 'messagingCampaigns': {
            return <>Work in progress</>
        }
        case 'campaignResults': {
            return <>Work in progress</>
        }
        case 'listRemainingRecords': {
            return <ListRemainingRecordsTab />
        }
        default: {
            return <>Unknown tab type</>
        }
    }
}
