import React, {useMemo} from 'react'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {campaignsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import type {CampaignDetailsTableColumnItem} from '@bpinc/ad-transport-types'
import {formatDurationShort} from '@bpinc/lib-helpers'
import {useLocalizeString} from '@bpinc/state-localization-context'


export const CampaignDetailsItem: React.FC<{
    column: CampaignDetailsTableColumnItem
}> = ({
    column,
}) => {
    const localize = useLocalizeString()
    const campaign = useSelectedCampaign()

    const campaignDefaultName = localize(campaignsColumnsLocalizationMap[column.id])

    const columnValue = useMemo(() => {
        const renderAnything = (value: unknown): React.ReactNode => {
            if (typeof value !== 'string' && typeof value !== 'number') {
                return ''
            }
            return `${value}`
        }

        const renderTime = (value: unknown): React.ReactNode => {
            if (typeof value !== 'string' && typeof value !== 'number') {
                return ''
            }

            const time = formatDurationShort(value)
            return time
        }

        if (!campaign || !column) {
            return ''
        }

        switch (column.id) {
            case 'out_calls_duration_average_per_day':
            case 'out_calls_duration_total_per_day':
            case 'in_max_wait_time': {
                return renderTime(campaign.fields[column.id])
            }
            case 'running': {
                return campaign.fields.state === 'running' ? 'Running' : 'Stopped'
            }
            case 'out_campaign_mode':
            case 'out_average_success_rate':
            case 'out_calls_in_ivr':
            case 'out_calls_active_on_agents':
            case 'out_calls_in_progress':
            case 'out_calls_current_call_rate':
            case 'out_campaign_duration_forecast':
            case 'agent_occupancy':
            case 'agents_logged':
            case 'agents_ready':
            case 'agents_busy':
            case 'agents_in_wrap_up':
            case 'agents_not_ready':
            case 'records_total':
            case 'records_dialable':
            case 'records_remaining':
            case 'records_completed':
            case 'records_completed_ratio':
            case 'records_in_dnc':
            case 'records_expired':
            case 'records_with_assigned_agents_completed':
            case 'records_with_assigned_agents_remaining':
            case 'out_calls_placed_per_day':
            case 'out_calls_answered_per_day':
            case 'out_calls_answered_ratio_per_day':
            case 'out_calls_failed_per_day':
            case 'out_calls_abandoned_per_day':
            case 'out_calls_abandoned_ratio_per_day':
            case 'out_calls_abandoned_in_ivr_per_day':
            case 'out_calls_abandoned_in_ivr_ratio_per_day':
            case 'out_calls_abandoned_in_queue_per_day':
            case 'out_calls_abandoned_in_queue_ratio_per_day':
            case 'out_calls_abandoned_ringing_per_day':
            case 'out_calls_unattended_per_day':
            case 'out_calls_unattended_ratio_per_day':
            case 'out_calls_dropped_in_ivr_per_day':
            case 'out_calls_dropped_in_queue_per_day':
            case 'out_calls_routed_per_day':
            case 'out_calls_handled_per_day':
            case 'out_calls_transferred_per_day':
            case 'out_calls_dropped_talking_per_day':
            case 'out_calls_disconnected_talking_per_day':
            case 'records_attempted_per_day':
            case 'records_attempted_ratio_per_day':
            case 'records_completed_per_day':
            case 'records_completed_ratio_per_day':
            default: {
                return renderAnything(campaign.fields[column.id as keyof typeof campaign.fields])
            }
        }
    }, [column, campaign])

    return (
        <div className="border-l p-2 pr-4">
            <div className="text-sm font-bold">
                {column.customName || campaignDefaultName}
            </div>

            <div className="mt-1 text-xl">
                {columnValue}
            </div>
        </div>
    )
}
