import {useCallback} from 'react'

import {useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {ManageMetricsModal} from '../../modals/ManageMetricsModal'
import type {GetOptions} from '../TableActionsButton'


export function useTableMenuOptions(): GetOptions {
    const {t} = useLocalizationState()
    const openManageMetricsModal = useModalComponentOpener(ManageMetricsModal)
    const selectedCampaign = useSelectedCampaign()
    const {startStopCampaign} = useCampaignsStateMethods()

    const isRunning = selectedCampaign?.fields.state === 'RUNNING'

    return useCallback(({toggleShow}) => [
        {
            children: t.SUPERVISION_PAGE.actions_menu_option_manage_metrics(),
            onClick: () => {
                openManageMetricsModal({campaignId: selectedCampaign?.id, table: 'campaignDetails'})
                toggleShow()
            },
        },
        {
            children: isRunning
                ? t.CAMPAIGN_MONITOR.campaign_button_stopCampaign()
                : t.CAMPAIGN_MONITOR.campaign_button_startCampaign(),
            onClick: () => {
                if (!selectedCampaign) {
                    return
                }
                startStopCampaign(selectedCampaign.id, !isRunning)
                toggleShow()
            },
        },
    ], [openManageMetricsModal, t, selectedCampaign, startStopCampaign, isRunning])
}
