import React, {useCallback, useEffect, useMemo} from 'react'

import {useCampaignsState, useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {campaignsListsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import type {CampaignListId, CampaignListsColumnGroup, CampaignListsColumnItem} from '@bpinc/ad-transport-types'
import {checkIsCampaignListsColumnGroup} from '@bpinc/ad-transport-types'
import type {ResizeTableColumnWidth, TableItem} from '@bpinc/lib-ui-elements'
import {ResizeTable, useSorting} from '@bpinc/lib-ui-elements'
import {useLocalizationState, useLocalizeString} from '@bpinc/state-localization-context'

import {useCollapsedTableGroupsIds} from '../../hooks/useCollapsedTableGroupsIds'
import {useSortedTableData} from '../../hooks/useSortedTableData'
import {useTableColumns} from '../../hooks/useTableColumns'
import {SectionHeader} from '../SectionHeader'
import {SectionTitle} from '../SectionTitle'
import {TableActionsButton} from '../TableActionsButton'
import {useItemRenderer} from './useItemRenderer'
import {useTableMenuOptions} from './useTableMenuOptions'


export const CampaignListsTable: React.FC = () => {
    const {t} = useLocalizationState()
    const localize = useLocalizeString()

    const {pageConfig, selectedListId, selectedCampaignId} = useCampaignsState()
    const {updatePageConfig, subscribe, unsubscribe, selectList} = useCampaignsStateMethods()
    const selectedCampaign = useSelectedCampaign()
    const {sortingSettings, handleSortingChange} = useSorting()
    const {collapsedGroupIds, toggleCollapsedGroup} = useCollapsedTableGroupsIds('lists')

    const {columns, groups} = useTableColumns({
        columnsConfig: pageConfig.CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG,
        collapsedGroupIds,
        localize: id => localize(campaignsListsColumnsLocalizationMap[id]) || id,
    })

    const data = useMemo<TableItem[]>(() => (selectedCampaign?.lists || []).map(item => ({
        id: item.id,
        ...item.metrics,
    })), [selectedCampaign?.lists])

    const sortedData = useSortedTableData({
        data,
        sortingSettings,
    })

    const handleRowClick = useCallback((row: TableItem) => {
        selectList(row.id as CampaignListId)
    }, [selectList])

    const selectedRows = useMemo(
        () => [selectedCampaign?.lists.findIndex(item => item.id === selectedListId) ?? -1],
        [selectedCampaign?.lists, selectedListId],
    )

    useEffect(() => {
        subscribe('lists')
        return () => unsubscribe('lists')
    }, [subscribe, unsubscribe, selectedCampaignId])

    const handleColumnsResize = useCallback((resizedColumns: Record<string, ResizeTableColumnWidth>) => {
        const newConfig = pageConfig.CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG.map(configItem => {
            if (!checkIsCampaignListsColumnGroup(configItem)) {
                return getUpdatedColumn(configItem)
            }

            const newGroup: CampaignListsColumnGroup = {
                ...configItem,
                columns: configItem.columns.map(getUpdatedColumn),
            }

            return newGroup

            function getUpdatedColumn(column: CampaignListsColumnItem) {
                if (!(column.id in resizedColumns)) {
                    return column
                }

                const newColumnWidth = resizedColumns[column.id]
                const newColumnItem: CampaignListsColumnItem = {
                    ...column,
                    width: newColumnWidth,
                }
                return newColumnItem
            }
        })

        updatePageConfig({
            ...pageConfig,
            CAMPAIGN_LISTS_TABLE_COLUMNS_CONFIG: newConfig,
        })
    }, [pageConfig, updatePageConfig])

    return (
        <>
            <SectionHeader className="mb-2">
                <SectionTitle title={t.CAMPAIGN_MONITOR.campaignMonitor_lists_heading()} />
                <TableActionsButton getOptions={useTableMenuOptions()} />
            </SectionHeader>

            <ResizeTable
                columns={columns}
                groups={groups}
                data={sortedData}
                sortingSettings={sortingSettings}
                onSortingChange={handleSortingChange}
                toggleCollapsedGroup={toggleCollapsedGroup}
                onColumnsResize={handleColumnsResize}
                isColumnResizable
                isResizable
                hasGrid
                onRowClick={handleRowClick}
                selectedRows={selectedRows}
                renderItem={useItemRenderer()}
                scrollEnabled
                defaultHeight={220}
            />
        </>
    )
}
