import React, {useEffect, useState} from 'react'

import type {DispositionsPieConfig} from '@bpinc/ad-transport-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {OnColorTitle} from './types'


export function Title({
    pieConfig,
    onChangeTitle,
}: {
    pieConfig: DispositionsPieConfig | undefined
    onChangeTitle?: OnColorTitle
}) {
    const {t} = useLocalizationState()

    const [editableTitle, setEditableTitle] = useState('')

    const defaultTitle = t.CAMPAIGN_MONITOR.default_pie_title()

    const setDefaultTitle = () => {
        setEditableTitle(pieConfig?.title || defaultTitle)
    }

    useEffect(() => { // update the title when the pieConfig changes
        setDefaultTitle()
    }, [pieConfig?.title])

    const onFocus = () => {
        setDefaultTitle()
    }

    const onBlur = () => {
        onChangeTitle?.(editableTitle)
        setDefaultTitle()
    }

    return (
        <div className="px-6">
            {onChangeTitle && (
                <input
                    className="w-full rounded-md border border-gray-300 bg-white px-4 py-2 text-xl"
                    value={editableTitle}
                    onChange={e => setEditableTitle?.(e.target.value)}
                    onFocus={onFocus}
                    onBlur={onBlur}
                />
            )}
            {!onChangeTitle && (
                <p className="text-xl">{editableTitle}</p>
            )}
        </div>
    )
}
