import React, {useCallback} from 'react'
import produce from 'immer'
import {useAtom} from 'jotai'

import type {DispositionsPieDisplayMode} from '@bpinc/ad-transport-types'
import {isHTMLInputElement} from '@bpinc/lib-html-utils'
import {Checkbox, Radio} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {editingConfigAtom} from '../atoms'
import {useTableItems} from './useTableItems'
import {useToggles} from './useToggles'


export function ShownDispositionsEditor() {
    const {t} = useLocalizationState()

    const {
        isAllOnLegend,
        isAllOnPie,
        isIndeterminateAllOnLegend,
        isIndeterminateAllOnPie,
        toggleIsAllOnLegend,
        toggleIsAllOnPie,
        changeIsShownOnLegend, changeIsShownOnPie,
    } = useToggles()

    const [editingConfig, setEditingConfig] = useAtom(editingConfigAtom)

    const onModeChange = useCallback((e: React.FormEvent<HTMLInputElement>) => {
        const input = e.target
        if (!isHTMLInputElement(input)) {
            return
        }
        const value = input.value as DispositionsPieDisplayMode

        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            prev.displayMode = value
        }))

    }, [setEditingConfig])

    const tableItems = useTableItems()

    return (
        <>
            <div className="mb-2 flex gap-2">
                <h3 className="mr-6 text-xl">{t.CAMPAIGN_MONITOR.displaying_mode()}</h3>
                <div className="flex">
                    <div className="mr-4 flex shrink-0 items-center">
                        <Radio
                            checked={editingConfig?.displayMode === 'dispositions'}
                            value="dispositions"
                            label="Dispositions"
                            name="displayMode"
                            onChange={onModeChange}
                        />
                    </div>
                    <div className="flex shrink-0 items-center">
                        <Radio
                            checked={editingConfig?.displayMode === 'groups'}
                            value="groups"
                            label="Groups"
                            name="displayMode"
                            onChange={onModeChange}
                        />
                    </div>
                </div>
            </div>

            <p className="mb-4">{t.CAMPAIGN_MONITOR.config_per_campaign_note()}</p>

            {editingConfig?.displayMode === 'dispositions' && (
                <div className="mt-4 pb-4">
                    <div className="grid grid-cols-[minmax(min-content,15rem),6rem,6rem] gap-1">
                        <div className="mb-1" />
                        <div className="mb-1 font-bold">{t.CAMPAIGN_MONITOR.display_on_pie()}</div>
                        <div className="mb-1 font-bold">{t.CAMPAIGN_MONITOR.display_in_list()}</div>

                        <div className="mb-1">{t.CAMPAIGN_MONITOR.select_all()}</div>
                        <div className="mb-1">
                            <Checkbox
                                checked={isAllOnPie}
                                indeterminate={isIndeterminateAllOnPie}
                                onChange={toggleIsAllOnPie}
                            />
                        </div>
                        <div className="mb-1">
                            <Checkbox
                                checked={isAllOnLegend}
                                indeterminate={isIndeterminateAllOnLegend}
                                onChange={toggleIsAllOnLegend}
                            />
                        </div>

                        {tableItems.map(it => (
                            <React.Fragment key={it.isGroup ? it.name : it.id}>
                                {it.isGroup
                                    ? (
                                        <div
                                            className="col-span-3 overflow-hidden text-ellipsis font-bold"
                                            title={it.name}
                                        >
                                            {it.name}
                                        </div>
                                    )
                                    : (
                                        <>
                                            <div
                                                className="overflow-hidden text-ellipsis pl-4"
                                                title={it.name}
                                            >
                                                {it.name}
                                            </div>
                                            <div>
                                                <Checkbox
                                                    checked={it.isShownOnPie}
                                                    onChange={() => {
                                                        changeIsShownOnPie(it.id, !it.isShownOnPie)
                                                    }}
                                                />
                                            </div>
                                            <div>
                                                <Checkbox
                                                    checked={it.isShownOnLegend}
                                                    onChange={() => {
                                                        changeIsShownOnLegend(it.id, !it.isShownOnLegend)
                                                    }}
                                                />
                                            </div>
                                        </>
                                    )}
                            </React.Fragment>
                        ))}
                    </div>
                </div>
            )}
        </>
    )
}
