import {useCallback, useMemo} from 'react'
import produce from 'immer'
import {useAtom} from 'jotai'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'

import {editingConfigAtom} from '../atoms'


export function useToggles() {
    const [editingConfig, setEditingConfig] = useAtom(editingConfigAtom)
    const selectedCampaign = useSelectedCampaign()

    const isAllOnLegend = useMemo(() => {
        return selectedCampaign?.dispositions?.every(disposition => {
            const dispositionConfig = editingConfig?.dispositions.find(it => it.dispositionId === disposition.id)
            return dispositionConfig?.isShownOnLegend
        })
    }, [selectedCampaign?.dispositions, editingConfig])

    const isIndeterminateAllOnLegend = useMemo(() => {
        const someShownOnLegend = selectedCampaign?.dispositions?.some(disposition => {
            const dispositionConfig = editingConfig?.dispositions.find(it => it.dispositionId === disposition.id)
            return dispositionConfig?.isShownOnLegend
        })
        return someShownOnLegend && !isAllOnLegend
    }, [selectedCampaign?.dispositions, editingConfig, isAllOnLegend])

    const isAllOnPie = useMemo(() => {
        return selectedCampaign?.dispositions?.every(disposition => {
            const dispositionConfig = editingConfig?.dispositions.find(it => it.dispositionId === disposition.id)
            return dispositionConfig?.isShownOnPie
        })
    }, [selectedCampaign?.dispositions, editingConfig])

    const isIndeterminateAllOnPie = useMemo(() => {
        const someShownOnPie = selectedCampaign?.dispositions?.some(disposition => {
            const dispositionConfig = editingConfig?.dispositions.find(it => it.dispositionId === disposition.id)
            return dispositionConfig?.isShownOnPie
        })
        return someShownOnPie && !isAllOnPie
    }, [selectedCampaign?.dispositions, editingConfig, isAllOnPie])

    const toggleIsAllOnLegend = useCallback(() => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            prev.dispositions.forEach(dispositionConfig => {
                dispositionConfig.isShownOnLegend = !isAllOnLegend
            })
        }))
    }, [isAllOnLegend, setEditingConfig])

    const toggleIsAllOnPie = useCallback(() => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            prev.dispositions.forEach(dispositionConfig => {
                dispositionConfig.isShownOnPie = !isAllOnPie
            })
        }))
    }, [isAllOnPie, setEditingConfig])

    const changeIsShownOnLegend = (dispositionId: string, isShownOnLegend: boolean) => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            const dispositionConfig = prev.dispositions.find(it => it.dispositionId === dispositionId)
            if (!dispositionConfig) {
                return
            }
            dispositionConfig.isShownOnLegend = isShownOnLegend
        }))
    }

    const changeIsShownOnPie = (dispositionId: string, isShownOnPie: boolean) => {
        setEditingConfig(produce(prev => {
            if (!prev) {
                return
            }
            const dispositionConfig = prev.dispositions.find(it => it.dispositionId === dispositionId)
            if (!dispositionConfig) {
                return
            }
            dispositionConfig.isShownOnPie = isShownOnPie
        }))
    }

    return {
        isAllOnLegend,
        isIndeterminateAllOnLegend,
        isAllOnPie,
        isIndeterminateAllOnPie,
        toggleIsAllOnLegend,
        toggleIsAllOnPie,
        changeIsShownOnLegend,
        changeIsShownOnPie,
    }
}
