import React, {useCallback, useEffect, useState} from 'react'

import {useCampaignsStateMethods} from '@bpinc/ad-campaigns-state-context'
import type {CampaignId} from '@bpinc/ad-transport-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Checkbox} from '@bpinc/lib-ui-elements'


type StartStopTogglerProps = {
    campaignId: CampaignId
    campaignState: string
}

export const StartStopToggle: React.FC<StartStopTogglerProps> = ({
    campaignId,
    campaignState,
}) => {
    const {startStopCampaign} = useCampaignsStateMethods()

    const isRunning = campaignState === 'RUNNING'

    const [isDisabled, setIsDisabled] = useState(false)

    const handleToggle = useCallback(async (isActive: boolean) => {
        if (isDisabled) {
            return
        }
        setIsDisabled(true)
        await startStopCampaign(campaignId, isActive)
    }, [startStopCampaign, campaignId, isDisabled])

    useEffect(() => { // when the request is done, we can allow the user to interact with the toggle again
        setIsDisabled(false)
    }, [campaignState])

    return (
        <Checkbox
            toggle
            checked={isRunning}
            disabled={isDisabled}
            labelClassName={tcn('min-w-[50px]')}
            onChange={handleToggle}
        />
    )
}
