import React, {useCallback, useEffect, useMemo} from 'react'

import {useCampaignsState, useCampaignsStateMethods} from '@bpinc/ad-campaigns-state-context'
import {campaignsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import {
    type CampaignId,
    type CampaignsTableColumnGroup,
    type CampaignsTableColumnItem,
    checkIsCampaignsTableColumnGroup,
} from '@bpinc/ad-transport-types'
import type {ResizeTableColumnWidth, TableItem} from '@bpinc/lib-ui-elements'
import {ResizeTable, useSorting} from '@bpinc/lib-ui-elements'
import {useLocalizationState, useLocalizeString} from '@bpinc/state-localization-context'

import {useCollapsedTableGroupsIds} from '../../hooks/useCollapsedTableGroupsIds'
import {useSortedTableData} from '../../hooks/useSortedTableData'
import {useTableColumns} from '../../hooks/useTableColumns'
import {SectionHeader} from '../SectionHeader'
import {SectionTitle} from '../SectionTitle'
import {TableActionsButton} from '../TableActionsButton'
import {useItemRenderer} from './useItemRenderer'
import {useTableMenuOptions} from './useTableMenuOptions'


export const CampaignsTable: React.FC = () => {
    const {t} = useLocalizationState()
    const localize = useLocalizeString()
    const {campaignItems, selectedCampaignId, pageConfig} = useCampaignsState()
    const {selectCampaign, updatePageConfig, subscribe, unsubscribe} = useCampaignsStateMethods()

    useEffect(() => {
        subscribe('campaigns')
        return () => unsubscribe('campaigns')
    }, [subscribe, unsubscribe])

    const {collapsedGroupIds, toggleCollapsedGroup} = useCollapsedTableGroupsIds('campaigns')

    const {columns, groups} = useTableColumns({
        columnsConfig: pageConfig.CAMPAIGNS_TABLE_COLUMNS_CONFIG,
        collapsedGroupIds,
        localize: id => localize(campaignsColumnsLocalizationMap[id]),
    })

    const handleRowClick = useCallback((row: TableItem) => {
        selectCampaign(row.id as CampaignId)
    }, [selectCampaign])

    const handleColumnsResize = useCallback((resizedColumns: Record<string, ResizeTableColumnWidth>) => {
        const newConfig = pageConfig.CAMPAIGNS_TABLE_COLUMNS_CONFIG.map(configItem => {
            if (!checkIsCampaignsTableColumnGroup(configItem)) {
                return getUpdatedColumn(configItem)
            }

            const newGroup: CampaignsTableColumnGroup = {
                ...configItem,
                columns: configItem.columns.map(getUpdatedColumn),
            }

            return newGroup

            function getUpdatedColumn(column: CampaignsTableColumnItem) {
                if (!(column.id in resizedColumns)) {
                    return column
                }

                const newColumnWidth = resizedColumns[column.id]
                const newColumnItem: CampaignsTableColumnItem = {
                    ...column,
                    width: newColumnWidth,
                }
                return newColumnItem
            }
        })

        updatePageConfig({
            ...pageConfig,
            CAMPAIGNS_TABLE_COLUMNS_CONFIG: newConfig,
        })
    }, [pageConfig, updatePageConfig])

    const {sortingSettings, handleSortingChange} = useSorting()

    const selectedRows = useMemo(
        () => [campaignItems.findIndex(item => item.id === selectedCampaignId)],
        [campaignItems, selectedCampaignId],
    )

    const data = useMemo<TableItem[]>(() => campaignItems.map(item => ({
        id: item.id,
        ...item.fields,
    })), [campaignItems])

    const sortedData = useSortedTableData({
        data,
        sortingSettings,
    })

    return (
        <div>
            <SectionHeader className="mb-2">
                <SectionTitle title={t.CAMPAIGN_MONITOR.campaignMonitor_heading()} />
                <TableActionsButton getOptions={useTableMenuOptions()} />
            </SectionHeader>

            <ResizeTable
                columns={columns}
                groups={groups}
                data={sortedData}
                sortingSettings={sortingSettings}
                onSortingChange={handleSortingChange}
                onRowClick={handleRowClick}
                toggleCollapsedGroup={toggleCollapsedGroup}
                onColumnsResize={handleColumnsResize}
                isColumnResizable
                isResizable
                hasGrid
                selectedRows={selectedRows}
                renderItem={useItemRenderer()}
                scrollEnabled
                defaultHeight={220}
            />
        </div>
    )
}
