import {useMemo} from 'react'
import {useAtom} from 'jotai'
import {groupBy} from 'lodash-es'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'

import {editingConfigAtom} from '../atoms'
import type {EditorDisposition, EditorGroup} from './types'


export function useTableItems() {
    const selectedCampaign = useSelectedCampaign()
    const [editingConfig] = useAtom(editingConfigAtom)

    return useMemo(() => {
        const grouped = groupBy(selectedCampaign?.dispositions || [], ({group}) => group)
        const result = Object.entries(grouped).reduce<Array<EditorDisposition | EditorGroup>>((acc, [groupName, dispositions]) => {
            acc.push({
                isGroup: true,
                name: groupName,
            })

            dispositions?.forEach(disposition => {
                const dispositionConfig = editingConfig?.dispositions.find(it => it.dispositionId === disposition.id)
                if (!dispositionConfig) {
                    return
                }

                acc.push({
                    id: disposition.id,
                    name: disposition.name,
                    isGroup: false,
                    isShownOnLegend: dispositionConfig.isShownOnLegend,
                    isShownOnPie: dispositionConfig.isShownOnPie,
                })
            })

            return acc
        }, [])

        return result
    }, [selectedCampaign?.dispositions, editingConfig])
}
