import React, {useEffect, useMemo} from 'react'

import {useCampaignsState, useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {defaultCampaignDetailsTableColumnsConfig} from '@bpinc/ad-campaigns-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SectionHeader} from '../SectionHeader'
import {SectionTitle} from '../SectionTitle'
import {TableActionsButton} from '../TableActionsButton'
import {CampaignDetailsItem} from './CampaignDetailsItem'
import {useTableMenuOptions} from './useTableMenuOptions'


export const CampaignDetailsTable: React.FC = () => {
    const t = useLocalizationState('t')
    const selectedCampaign = useSelectedCampaign()
    const {subscribe, unsubscribe} = useCampaignsStateMethods()
    const {pageConfig, selectedCampaignId} = useCampaignsState()

    const columns = useMemo(() => {
        if (!pageConfig || !selectedCampaign) {
            return []
        }

        const campaignColumnsConfig =
            pageConfig.CAMPAIGN_DETAILS_TABLE_COLUMNS_CONFIG[selectedCampaign.id]
            || defaultCampaignDetailsTableColumnsConfig

        const visibleColumns = campaignColumnsConfig.filter(column => column.isShown)

        return visibleColumns
    }, [pageConfig, selectedCampaign])

    const getMenuOptions = useTableMenuOptions()

    useEffect(() => {
        subscribe('details')
        return () => unsubscribe('details')
    }, [subscribe, unsubscribe, selectedCampaignId])

    return (
        <div>
            <SectionHeader className="mb-2">
                <SectionTitle title={t.CAMPAIGN_MONITOR.campaign_details_title(selectedCampaign?.fields?.name || '-')} />
                <TableActionsButton getOptions={getMenuOptions} />
            </SectionHeader>

            <div className="mt-2 flex flex-wrap gap-y-2">
                {!selectedCampaign
                    && (
                        <div className="text-sm text-gray-500">
                            {t.CAMPAIGN_MONITOR.no_campaign_selected()}
                        </div>
                    )}

                {columns.map(column => (
                    <CampaignDetailsItem
                        key={column.id}
                        column={column}
                    />
                ))}
            </div>
        </div>
    )
}
