import React, {useEffect} from 'react'

import {useCampaignsState, useCampaignsStateMethods, useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import {SystemIcon} from '@bpinc/lib-icons'
import {Button} from '@bpinc/lib-ui-elements'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {DispositionsPieEditorModal} from '../../modals/DispositionsPieEditorModal'
import {ControlledDispositionsPie} from '../ControlledDispositionsPie'


export const DispositionsPie: React.FC = () => {
    const selectedCampaign = useSelectedCampaign()
    const openRecordDispositionsPieModal = useModalComponentOpener(DispositionsPieEditorModal)
    const {pageConfig, selectedCampaignId} = useCampaignsState()
    const {subscribe, unsubscribe} = useCampaignsStateMethods()

    const selectedCampaignPieConfig = selectedCampaign?.id
        ? pageConfig.DISPOSITIONS_PIE_CONFIG[selectedCampaign.id]
        : undefined

    useEffect(() => {
        subscribe('pie')
        return () => unsubscribe('pie')
    }, [subscribe, unsubscribe, selectedCampaignId])

    return (
        <div className="w-[500px] shrink-0">
            <div className="mb-2 flex h-6 justify-end">
                <Button
                    clickThrottle={500}
                    onClick={() => openRecordDispositionsPieModal()}
                >
                    <SystemIcon className="size-5" />
                </Button>
            </div>

            <ControlledDispositionsPie
                pieConfig={selectedCampaignPieConfig}
                className="max-h-60"
            />
        </div>
    )
}
