import {useCampaignsState, useCampaignsStateMethods} from '@bpinc/ad-campaigns-state-context'
import type {CampaignId, CampaignListId} from '@bpinc/ad-transport-types'
import {useEvent} from '@bpinc/lib-helpers'
import {uuid} from '@bpinc/lib-uuid'

/**
 * Go to the remaining records tab.
 * If the tab is already opened, it will be selected.
 * If the tab is not opened, it will be opened and selected.
 */
export function useGoRemainingRecords() {
    const {tabs} = useCampaignsState()
    const {openTab, selectTab} = useCampaignsStateMethods()

    return useEvent(({
        listId,
        campaignId,
    }: {
        listId: CampaignListId
        campaignId: CampaignId
    }) => {
        const existingTab = tabs.find(it => it.type === 'listRemainingRecords'
            && it.campaignId === campaignId
            && it.listId === listId)

        if (existingTab) {
            selectTab(existingTab.id)
            return
        }

        const newTabId = uuid()
        openTab({
            id: newTabId,
            type: 'listRemainingRecords',
            campaignId,
            listId,
        })
        selectTab(newTabId)
    })
}
