import {calculateTextWidth} from '@bpinc/lib-helpers'


type GetFitStringsAfter = ({restCount}: {restCount: number}) => string

/**
 * Get the strings that fit in a container width
 */
export function getFitStrings({
    strings,
    font,
    containerWidth,
    after,
}: {
    /** The strings to fit */
    strings: string[]
    /** The width of the container */
    containerWidth: number
    /** The font to use. Example: 'italic small-caps bold 12px/30px Arial' */
    font: string
    /** * A function that returns the text to append after the fitted strings */
    after?: GetFitStringsAfter
}) {
    const {fittedStrings} = strings.reduce<{
        isEnough: boolean
        fittedStrings: string[]
    }>((acc, it) => {
        if (acc.isEnough) {
            return acc
        }

        const nextFittedStrings = [...acc.fittedStrings, it]
        const nextRestCount = strings.length - nextFittedStrings.length
        const afterText = after?.({restCount: nextRestCount}) || ''
        const nextText = nextFittedStrings.join(', ') + afterText
        const nextTextWidth = calculateTextWidth(nextText, font)

        if (nextTextWidth > containerWidth) {
            acc.isEnough = true
        } else {
            acc.fittedStrings = nextFittedStrings
        }

        return acc

    }, {
        isEnough: false,
        fittedStrings: [],
    })

    return fittedStrings
}
