import React from 'react'

import {useSelectedCampaign} from '@bpinc/ad-campaigns-state-context'
import type {CampaignListId} from '@bpinc/ad-transport-types'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useGoRemainingRecords} from './useGoRemainingRecords'


export const Remaining: React.FC<{
    listId: CampaignListId
    children: React.ReactNode
}> = ({
    listId,
    children,
}) => {
    const selectedCampaign = useSelectedCampaign()
    const goRemainingRecords = useGoRemainingRecords()

    const onClick = () => {
        assertDefined(selectedCampaign)
        goRemainingRecords({
            campaignId: selectedCampaign.id,
            listId,
        })
    }

    return (
        <button
            className="w-full overflow-hidden text-link hover:underline"
            onClick={onClick}
        >
            {children}
        </button>
    )
}
