import {useMemo} from 'react'

import type {GroupedTableColumnItem, GroupedTableColumnsConfig} from '@bpinc/ad-transport-types'
import {checkIsGroupedTableColumnGroup} from '@bpinc/ad-transport-types'
import type {ResizeTableColumn, ResizeTableColumnGroup} from '@bpinc/lib-ui-elements'


export const useTableColumns = <T extends string>({
    columnsConfig,
    collapsedGroupIds,
    localize,
}: {
    columnsConfig?: GroupedTableColumnsConfig<T>
    collapsedGroupIds?: {[groupId: string]: boolean}
    localize: (id: T) => string
}) => {
    const columnsConfigWithoutHidden = useMemo(() => {
        if (!columnsConfig) {
            return []
        }

        return columnsConfig.reduce<GroupedTableColumnsConfig<T>>((acc, item, i) => {
            if (checkIsGroupedTableColumnGroup(item)) {
                const collapsed = !!collapsedGroupIds?.[item.id]
                const columns = item.columns.filter(c => c.isShown)

                return [
                    ...acc,
                    {
                        ...item,
                        columns: collapsed
                            ? columns.slice(0, 1)
                            : columns,
                    },
                ]
            }

            return item.isShown
                ? [...acc, item]
                : acc
        }, [])
    }, [columnsConfig, collapsedGroupIds])

    const columns = useMemo(() => {
        const flatColumns = columnsConfigWithoutHidden.flatMap(it => {
            if (checkIsGroupedTableColumnGroup(it)) {
                return it.columns
            }

            return it
        })

        return flatColumns.map(configToTableColumn)

        function configToTableColumn(item: GroupedTableColumnItem<T>): ResizeTableColumn {
            return {
                name: item.id,
                title: item.customName || localize(item.id),
                width: item.width,
                align: 'start',
                sticky: item.id === 'name',
            }
        }
    }, [columnsConfigWithoutHidden, localize])

    const groups = useMemo<ResizeTableColumnGroup[]>(() => {
        // we need to wrap all root columns into groups
        return columnsConfigWithoutHidden.reduce<ResizeTableColumnGroup[]>((acc, item, idx, arr) => {
            if (checkIsGroupedTableColumnGroup(item)) {
                if (!item.columns.length) {
                    return acc
                }

                const collapsed = !!collapsedGroupIds?.[item.id]

                acc.push({
                    id: item.id,
                    content: item.customName || localize(item.id),
                    colspan: collapsed ? 1 : item.columns.length,
                    collapsed,
                })

                return acc
            }

            const prevItem = arr[idx - 1]

            // this item is not a group, and the previous one is not a group,
            // so we need to increment the colspan of the last group
            // it means that the current item is added to the previous group
            if (!checkIsGroupedTableColumnGroup(prevItem)) {
                let lastGroup = acc[acc.length - 1]

                // if there is no group yet, create a new one
                // for the first item
                if (!lastGroup) {
                    lastGroup = {
                        colspan: 0,
                        sticky: false,
                        collapsed: false,
                    }
                    acc.push(lastGroup)
                }

                lastGroup.colspan++
                return acc
            }

            // this item is not a group, and the previous one is a group,
            // so we need to add a new group
            acc.push({
                colspan: 1,
                sticky: false,
                collapsed: false,
            })

            return acc
        }, [])
    }, [columnsConfigWithoutHidden, collapsedGroupIds, localize])

    return {
        columns,
        groups,
    }
}
