import React, {useMemo} from 'react'

import {Chart as Highchart} from '@bpinc/lib-highcharts'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const Chart: React.FC<{
    postponedTodayAM: number
    postponedTodayPM: number
    postponedTomorrowAM: number
    postponedTomorrowPM: number
    postponedLater: number
}> = ({
    postponedTodayAM,
    postponedTodayPM,
    postponedTomorrowAM,
    postponedTomorrowPM,
    postponedLater,
}) => {
    const {t} = useLocalizationState()
    const options = useMemo(() => {
        return {
            chart: {
                type: 'bar',
                inverted: true,
                animation: false,
            },
            title: {
                text: '',
            },
            xAxis: {
                categories: [
                    t.CAMPAIGN_MONITOR.today_am(),
                    t.CAMPAIGN_MONITOR.today_pm(),
                    t.CAMPAIGN_MONITOR.tomorrow_am(),
                    t.CAMPAIGN_MONITOR.tomorrow_pm(),
                    t.CAMPAIGN_MONITOR.later(),
                ],
                title: {
                    text: null,
                },
            },
            yAxis: {
                title: {
                    text: '',
                },
                labels: {
                    format: '{value}',
                },
            },
            plotOptions: {
                series: {
                    animation: false,
                    dataLabels: {
                        enabled: true,
                    },
                },
            },
            tooltip: {
                enabled: false,
            },
            legend: {
                enabled: false,
            },
            series: [
                {
                    type: 'bar',
                    data: [
                        {y: postponedTodayAM},
                        {y: postponedTodayPM},
                        {y: postponedTomorrowAM},
                        {y: postponedTomorrowPM},
                        {y: postponedLater},
                    ],
                },
            ],
        } satisfies Highcharts.Options
    }, [
        postponedTodayAM,
        postponedTodayPM,
        postponedTomorrowAM,
        postponedTomorrowPM,
        postponedLater,
        t,
    ])

    return (
        <div className="h-[345px]">
            <Highchart options={options} />
        </div>
    )
}
