import {useCallback, useMemo} from 'react'

import {campaignsColumnsLocalizationMap} from '@bpinc/ad-campaigns-state-types'
import {
    type CampaignsTableColumn,
    type CampaignsTableColumnGroup,
    type CampaignsTableColumnItem,
    type CampaignsTableColumnsConfig,
    checkIsCampaignsTableColumnGroup,
} from '@bpinc/ad-transport-types'
import type {ColumnsEditorColumn, ColumnsEditorConfig, ColumnsEditorGroup} from '@bpinc/lib-ui-elements'
import {useLocalizeString} from '@bpinc/state-localization-context'


type CampaignsTableMeta = {
    beforeEditing: CampaignsTableColumnGroup | CampaignsTableColumnItem
}

export function useCampaignsTableConfigAdapter() {
    const toEditing = useConvertConfigToEditing()

    return useMemo(() => ({
        toEditing,
        fromEditing,
    }), [toEditing])
}

function useConvertConfigToEditing() {
    const localize = useLocalizeString()

    return useCallback((config: CampaignsTableColumnsConfig) => {
        return config.map(it => {
            if (checkIsCampaignsTableColumnGroup(it)) {
                const group: ColumnsEditorGroup<CampaignsTableMeta> = {
                    id: it.id,
                    defaultName: localize(campaignsColumnsLocalizationMap[it.id]),
                    customName: it.customName,
                    isGroup: true,
                    columns: it.columns.map(columnToTreeItem),
                    meta: {
                        beforeEditing: it,
                    },
                }

                return group
            }

            const item = columnToTreeItem(it)

            return item


            function columnToTreeItem(configItem: CampaignsTableColumnItem): ColumnsEditorColumn<CampaignsTableMeta> {
                return {
                    id: configItem.id,
                    defaultName: localize(campaignsColumnsLocalizationMap[configItem.id]),
                    customName: configItem.customName,
                    isActive: configItem.isShown,
                    meta: {
                        beforeEditing: configItem,
                    },
                }
            }
        })
    }, [localize])
}

function fromEditing(configItem: ColumnsEditorConfig<CampaignsTableMeta> | undefined): CampaignsTableColumnsConfig {
    const newConfig = configItem?.map<CampaignsTableColumnGroup | CampaignsTableColumnItem>(it => {
        if (it.isGroup) {
            const group: CampaignsTableColumnGroup = {
                ...it.meta?.beforeEditing,
                id: it.id as CampaignsTableColumn,
                customName: it.customName,
                columns: it.columns.map(treeItemToColumn),
            }

            return group
        }

        return treeItemToColumn(it)

        function treeItemToColumn(item: ColumnsEditorColumn<CampaignsTableMeta>): CampaignsTableColumnItem {
            const itemMetaBeforeEditing = item.meta?.beforeEditing
            if (!itemMetaBeforeEditing || checkIsCampaignsTableColumnGroup(itemMetaBeforeEditing)) {
                throw new Error('Invalid column meta data')
            }
            return {
                ...itemMetaBeforeEditing,
                customName: item.customName,
                isShown: item.isActive,
            }
        }
    })

    return newConfig || []
}
