import React, {useMemo} from 'react'

import {useBoolean} from '@bpinc/lib-helpers'
import {IconTableActions} from '@bpinc/lib-icons'
import type {PopupMenuOption} from '@bpinc/lib-ui-elements'
import {PopupButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export type GetOptions = (args: {toggleShow: () => void}) => PopupMenuOption[]

export const TableActionsButton: React.FC<{
    getOptions: GetOptions
}> = ({
    getOptions,
}) => {
    const {t} = useLocalizationState()
    const {value: show, toggle: toggleShow} = useBoolean()

    const options = useMemo(() => getOptions({toggleShow}), [getOptions, toggleShow])

    return (
        <PopupButton
            icon
            basic
            show={show}
            toggleShow={toggleShow}
            options={options}
            className="flex size-6 items-center justify-center p-0 shadow-none"
            tooltip={t.SUPERVISION_PAGE.actions_menu_btn_tooltip()}
        >
            <IconTableActions />
        </PopupButton>
    )
}
