import React, {useEffect} from 'react'
import classNames from 'classnames'

import {useCampaignsState, useCampaignsStateMethods, useSelectedTab} from '@bpinc/ad-campaigns-state-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {SmMessage, SmProgress} from '@bpinc/lib-external-ui-elements'
import {useEvent} from '@bpinc/lib-helpers'
import {IconRefresh24} from '@bpinc/lib-jskit-ui'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Chart} from './Chart'


export const ListRemainingRecordsTab: React.FC = () => {
    const {t} = useLocalizationState()
    const selectedTab = useSelectedTab()

    // The component is shown only when the selected tab is of type 'listRemainingRecords'
    if (selectedTab?.type !== 'listRemainingRecords') {
        throw new Error('Invalid tab type')
    }

    const {generateListReport} = useCampaignsStateMethods()
    const {campaignItems} = useCampaignsState()

    const campaign = campaignItems.find(it => it.id === selectedTab.campaignId)
    const list = campaign?.lists.find(it => it.id === selectedTab.listId)

    const postponedSum = (() => {
        if (!list?.reportData) {
            return 0
        }

        return Number(list.reportData.scheduled_today_am) +
            Number(list.reportData.scheduled_today_pm) +
            Number(list.reportData.scheduled_tomorrow_am) +
            Number(list.reportData.scheduled_tomorrow_pm) +
            Number(list.reportData.scheduled_later)
    })()

    const generateReport = useEvent(() => {
        generateListReport(selectedTab.campaignId, selectedTab.listId)
    })

    // Generate the report when the report is not loaded yet
    useEffect(() => {
        if (list?.isReportLoading || list?.reportData) {
            return
        }
        generateReport()
    }, [])

    return (
        <>
            <div className="mb-2.5 mt-4 flex items-center gap-3">
                <h1 className="text-xl font-bold">{t.CAMPAIGN_MONITOR.remaining_records()}</h1>
                <div className="h-4.5 w-0.5 bg-[--c-icon-default]" />
                <p className="text-xl">{campaign?.fields.name || ''}</p>
                <div className="h-4.5 w-0.5 bg-[--c-icon-default]" />
                <p className="text-xl">{list?.metrics.name || ''}</p>
                <div className="h-4.5 w-0.5 bg-[--c-icon-default]" />
                <p className="text-xl">
                    {list?.reportAt ? formatDate(list.reportAt, {prefixed: true, full: true}) : ''}
                </p>
            </div>

            {list?.isReportLoading && (
                <>
                    <SmMessage className="max-w-[720px] shrink-0" info>
                        {t.CAMPAIGN_MONITOR.generating_report()}
                    </SmMessage>

                    <div className="mt-2.5 flex gap-6">
                        <div className="h-8 pt-1 font-bold">Progress</div>
                        <div className="w-52 pt-1">
                            <SmProgress percent={100} indicating label={`0 of ${list?.metrics.records_total}`} />
                        </div>
                        <div>
                            <SemanticButton className="py-1.5">
                                {t.GLOBAL.cancel()}
                            </SemanticButton>
                        </div>
                    </div>
                </>
            )}

            {list && !list.isReportLoading && list.reportData && (
                <>
                    <div className="mt-4 flex">
                        <div
                            className={classNames(
                                'w-60 p-6 text-center text-default shadow-[0px_2px_6px_#00000033]',
                                {
                                    'bg-[--c-interaction-bar-bg] text-white': Number(list?.reportData?.available || 0) > 0,
                                },
                            )}
                        >
                            <div className="mb-2 text-xl font-bold">{list.reportData.available}</div>
                            <div>{t.CAMPAIGN_MONITOR.available_now()}</div>
                        </div>

                        <div
                            className={classNames(
                                'w-60 p-6 text-center text-default shadow-[0px_2px_6px_#00000033]',
                                {
                                    'bg-gray-200': postponedSum > 0,
                                },
                            )}
                        >
                            <div className="mb-2 text-xl font-bold">{postponedSum}</div>
                            <div>{t.CAMPAIGN_MONITOR.postponed()}</div>
                        </div>

                        <div
                            className={classNames(
                                'w-60 p-6 text-center text-default shadow-[0px_2px_6px_#00000033]',
                                {
                                    'bg-red-600 text-white': Number(list.reportData.out_of_calling_hours || 0) > 0,
                                },
                            )}
                        >
                            <div className="mb-2 text-xl font-bold">{list.reportData.out_of_calling_hours}</div>
                            <div>{t.CAMPAIGN_MONITOR.misconfigured()}</div>
                        </div>

                        <button
                            className="ml-4 flex h-8 w-8 items-center justify-center rounded bg-gray-200 hover:bg-gray-300"
                            onClick={generateReport}
                        >
                            <IconRefresh24 />
                        </button>
                    </div>

                    <div className="mt-4 max-w-[720px] px-4 py-2 shadow-[0px_2px_6px_#00000033]">
                        <h2 className="mb-6 text-xl">
                            {t.CAMPAIGN_MONITOR.postponed_until()}
                        </h2>

                        <Chart
                            postponedTodayAM={Number(list.reportData.scheduled_today_am) || 0}
                            postponedTodayPM={Number(list.reportData.scheduled_today_pm) || 0}
                            postponedTomorrowAM={Number(list.reportData.scheduled_tomorrow_am) || 0}
                            postponedTomorrowPM={Number(list.reportData.scheduled_tomorrow_pm) || 0}
                            postponedLater={Number(list.reportData.scheduled_later) || 0}
                        />
                    </div>
                </>
            )}
        </>
    )
}
